import React, { Component } from 'react'
import "./style.scss"

export default class iscroll extends Component {
    constructor(props){
        super(props);
        this.top = props.top;
        this.bottom = props.bottom;
    }

    render() {
        return (
            <div className="wrapper" style={{top:this.top,bottom:this.bottom||"0px"}} ref="myscroll">
                 <div className="homeCon">
                     {this.props.children}
                  </div>
            </div>
        )
    }

    IscorllAction(){
        window.myScorll.refresh();
    }

    componentDidMount(){
        let IScroll = window.IScroll;
        window.myScorll = new IScroll(this.refs.myscroll,{
           click:true,
           bounce:true,
           mouseWheel: true,
         });
     
         window.myScorll.on("beforeScrollStart",this.IscorllAction)
   }
}
