import React, { Component, } from 'react'
import Haeder from '../components/header'
import MyIscorll from "../../common/myiscroll/myiscroll"
import "./style.scss"

export default class preview extends Component {
    constructor(props){
        super(props);
        this.state={
            article:this.props.location.state,
            article2: {"title":"向诸葛亮借智慧",
            "catalog":"历史 心理学 管理 ",
            "tags":"一本好书 三国历史 社会学好书推荐 管理书籍推荐 诸葛亮 ",
            "sub1":"《向诸葛亮借智慧》",
            "sub2":"《向诸葛亮借智慧》是中央电视台《百家讲坛》主讲人赵玉平老师编著的一本管理学类图书。作品从管理学和心理学的角度出发，运用人力资源管理和组织行为学的知识，解读了三国睿智军师诸葛亮的一生。本书对三顾茅庐、舌战群儒、赤壁大战、空城计、斩马谡、六出祁山、七擒孟获等与诸葛亮相关的大事件进行了精辟的分析。\n诸葛亮（公元181年7月23日—234年8月28日），三国时期蜀国丞相，字孔明，号卧龙，琅琊阳都人，是三国时期杰出的政治家、军事家、发明家和文学家。诸葛亮上知天文、下知地理，雄才大略、才华横溢。为匡扶蜀汉政权，他呕心沥血，鞠躬尽瘁，死而后已。唐朝诗人杜甫有诗赞道：“三顾频烦天下计，两朝开济老臣心。出师未捷身先死，长使英雄泪满襟。”，可见诸葛亮受到后世人们极大的尊崇，是后世忠臣的楷模、智慧的化身。\n《向诸葛亮借智慧》一书是管理学教授赵玉平“麻辣说三国”系列中的一部文学作品。赵玉平老师同易中天教授一起被权威机构评选为“2009中国十大国学专家”，是运用管理学、国学、心理学综合解析《水浒传》、三国历史人物的中国第一人，开辟了运用管理学和心理学解读传统经典的新领域。\n隆中对策、舌战群儒、草船借箭、七擒孟获……一个个璀璨如明珠的经典故事，成就了诸葛亮无人企及的智慧传奇。但是，孔明先生的智慧究竟从何而来呢？在千百年后的今天，他的卓越智慧又能给今天的我们带来哪些启迪和帮助呢？《向诸葛亮借智慧》一书从一个崭新的角度观察诸葛亮这个重要的历史人物和相关的历史事件，讲故事的同时也分析规律，并结合当今现实生活，古为今用，总结出能够运用于求职、求人、用人、管人等现实生活中的规律和技巧。\n鲁迅说：“关公之义近乎伪，诸葛之智近乎妖。” 《向诸葛亮借智慧》，哪怕只借到了一点点，也足够让我们的生活过得更轻松、更顺心了。\n《向诸葛亮借智慧》",
            "img":"http://apis.juhe.cn/goodbook/img/379cdafe13f92d62e99388182a6d08ec.jpg",
            "reading":"8091人阅读",
            "online":"京东商城:http://book.jd.com/10483893.html 当当网:http://product.dangdang.com/product.aspx?product_id=21020821 苏宁易购:http://www.suning.com/emall/prd_10052_22001_-7_1006212_.html",
            "bytime":"2013年4月28日"}
        }
    }
    

    render() {
        let {article,article2} = this.state;
          article = article||article2;
        return (
            <div className="page" id="preview">
                <Haeder
                 back={()=>this.props.history.goBack()}
                 centent="预览"/>
                <MyIscorll top="44px">
                <div id="content">
                    <div className="top">
                       <img src={article.img} alt="图片"/>
                       <div className="right">
                           <h3>{article.sub1}</h3>
                           <p>类别：{article.catalog}</p>
                           <p>作者：那年的天</p>
                           <p>出版时间：{article.bytime}</p>
                       </div>
                    </div>
                    <div className="main">
                      <h4 style={{fontWeight:"bolder",paddingBottom:"10px"}}>简介：</h4>
                      <p style={{padding:"10px"}}>标签：{article.tags}</p>
                      {article.sub2}
                      <h3 style={{padding:"10px",textAlign:"right"}}>
                         {article.reading}</h3>
                    </div>
                </div>
                </MyIscorll>
            </div>
        )
    }
}
